.footer {
  background: #000;
  padding: 50px 0;

  @include mobile {
    padding: 30px 0;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include tablet {
      flex-wrap: wrap;
      gap: 30px;
    }

    @include small-mobile {
      flex-direction: column;
    }
  }

  &__item {
    margin: 0 auto;
  }

  &__logo {
    width: 100px;
    height: auto;

    img {
      object-fit: cover;
    }

    @include mobile {
      width: 80px;
    }
  }

  &__policy {
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    color: rgba(255, 255, 255, 0.5);

    @include big-tablet {
      font-size: 16px;
      line-height: 20px;
    }

    @include mobile {
      text-align: center;
    }
  }

  &__call {
    padding: 20px 30px;
    text-align: center;
    background: $mainColor;
    max-width: 280px;
    width: 100%;
    transition: all 0.4s;

    &:focus,
    &:hover {
      background: $mainColorHover;
    }

    @include big-tablet {
      max-width: 225px;
    }
  }

  &__number {
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;

    @include tablet {
      font-size: 20px;
      line-height: 24px;
    }

    @include small-mobile {
      font-size: 18px;
      line-height: 20px;
    }
  }
}
