.gallery {
  margin: 0 0 80px;

  @include mobile {
      margin: 0 0 50px;
  }

  &__title {
    font-weight: 600;
    font-size: 30px;
    line-height: 170%;
    letter-spacing: 0.15em;
    margin-bottom: 60px;

    @include mobile {
        line-height: 140%;
        font-size: 26px;
        margin-bottom: 30px;
    }

    @media (max-width: 400px) {
        font-size: 24px;
        margin-bottom: 20px;
    }

    span {
      color: $whiteColor;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    position: relative;
  }

  &-item {
    text-align: center;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__slider {
    width: 100%;
  }

  .swiper-slide {
    width: 100%;
    height: 100%;
    display: flex;
    margin: auto;
    align-items: center;
    justify-content: center;
    max-width: 800px;
    max-height: 440px;


    @media (max-width: 1060px) {
      max-width: 525px;
      max-height: 366px;
    }

    @media (max-width: 782px) {
      max-width: 460px;
      max-height: 230px;
    }

    @media (max-width: 680px) {
        max-width: 80%;
        max-height: 100%;
    }
  }

  &-navigation {
    display: flex;
    align-items: center;
    justify-content: space-between;

    content: '';
    position: absolute;
    width: calc(100% - 600px);
    height: 50px;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10;

    @media (max-width: 1460px) {
      width: calc(100% - 120px);
    }

    @media (max-width: 782px) {
      position: static;
      max-width: 100px;
      gap: 20px;
    }

  }

  &-arrow {
    cursor: pointer;
    width: 50px;
    height: 50px;
    svg {
      object-fit: cover;
      width: 100%;
    }
    
    @media (max-width: 782px) {
      width: 50px;
      height: 50px;
      padding: 14px 13px;
      background: #FF9E0C;
      border-radius: 50%;
      margin-top: 80px;

      svg {
        width: 26px;
        height: 26px;
      }
    }
    @media (max-width: 360px) {
      width: 44px;
      height: 44px;
      padding: 14px 13px;
      background: #FF9E0C;
      border-radius: 50%;
      margin-top: 80px;

      svg {
        width: 26px;
        height: 26px;
      }
    }

    @media (max-width: 400px) {
        width: 38px;
        height: 38px;
        padding: 9px 10px;
        border-radius: 50%;
  
        svg {
          width: 20px;
          height: 20px;
        }
      }
  }
}

._disabled-popup {
    display: none
}

._active-popup {
    display: flex;
    justify-content: center;
    align-items: center
}

._active-popup img {
    width: 100%;
    height: 100%;
    max-width: 1100px;
    max-height: 800px;
    object-fit: cover
}