.offer {
  padding: 70px 0 0;

  @include mobile {
    padding: 50px 0 0;
  }

  @include small-mobile {
    padding: 40px 0 0;
  }

  &__container {
    overflow: hidden;
  }

  &__title {
    font-weight: 600;
    font-size: 36px;
    line-height: 40px;

    @include mobile {
      font-size: 24px;
      line-height: 28px;
    }

    span {
      color: $whiteColor;
    }
  }

  &-item {
    max-width: 330px;
    max-height: 320px;
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    position: relative;
    height: 320px;

    @media (min-width: 1920px) {
    max-width: 450px;
    max-height: 360px;
    }

    @media (max-width: 1280px) {
      max-width: 33%;
    }

    @include tablet {
      max-width: 50%;
    }

    @include mobile {
      max-width: 100%;
      margin-top: 20px;
    }

    &__img {
      border-radius: 25px;
      max-width: 100%;
      max-height: 220px;
      width: 100%;
      height: 100%;
      object-fit: cover;
      margin: 0 auto;
      position: absolute;
      top: 0;
      left: 0;
      @include mobile {
        max-height: 230px;
      }
    }

    &__cost {
      display: block;
      color: $whiteColor;
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      text-align: center;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;

      @include small-mobile {
        font-size: 16px;
        line-height: 20px;
      }
    }

    &__links {
      display: flex;
      align-items: center;
      text-align: center;
      margin: 0 auto;
      justify-content: space-between;
      position: absolute;
      bottom: 66px;
      left: 0;
      width: 100%;

      @include mobile {
        bottom: 55px;
      }
    }

    &__link {
      font-weight: 600;
      font-size: 12px;
      line-height: 16px;
      color: $whiteColor;
      transition: all 0.4s;
      padding: 10px 15px;
      width: 50%;
      height: 52px;
      text-align: center;
      transition: background 0.2s ease-in;

      @include small-mobile {
        font-size: 14px;
        line-height: 16px;
      }
    }
  }

  &__slider {
    margin-bottom: 70px;

    @include mobile {
      margin-bottom: 40px;
    }
  }

  .swiper-wrapper {
    display: flex;
    flex-direction: row;
    align-items: first baseline;
    margin: 0 auto;
  }

  .swiper-pagination-bullets {
    margin: 40px auto 0;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;

    @include mobile {
      margin: 30px auto 0;
    }
  }

  .swiper-pagination-bullet {
    margin: 0 auto;
    background: none;
    width: 10px;
    height: 25px;
    text-align: center;
    font-size: 22px;
    line-height: 22px;
    color: rgba($whiteColor, 0.6);
    transition: color 0.3s fade-in;

    &:not(:last-child) {
      margin-right: 20px;
    }

    @include small-mobile {
      width: 8px;
      height: 20px;
      font-size: 20px;
      line-height: 20px;
    }
  }

  .swiper-pagination-bullet-active {
    color: #fff;
  }

  &-watch {
    background: #506fb2;
    border-bottom-right-radius: 25px;
    &:hover {
      background: #3d5a97;
    }
  }

  &-download {
    background: #ae9a64;
padding: 15px 5px;
border-bottom-left-radius: 25px;

&:hover {
  background: #978553;
}
  }
}
