.country {
  padding: 40px 0 80px;

  @include mobile {
    padding: 30px 0 60px;
  }


  &__title {
    position: relative;
    font-weight: 600;
    font-size: 36px;
    line-height: 40px;
    text-transform: uppercase;
    margin-bottom: 50px;

    @include mobile {
      font-size: 36px;
      line-height: 44px;
      margin-bottom: 40px;
    }

    @include small-mobile {
      font-size: 28px;
      line-height: 32px;
    }
  }

  &__items {
    max-width: 70%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    column-gap: 60px;
    row-gap: 60px;

    @include mobile {
      column-gap: 30px;
      row-gap: 20px;
    }
  }

  &__item {
    font-weight: normal;
    font-size: 28px;
    line-height: 30px;
    color: rgba($whiteColor, 0.5);
    white-space: nowrap;
    transition: all 0.3s ease-in;
    &:focus, &:hover {
      color: $whiteColor;
    }

    @include mobile {
      font-size: 24px;
      line-height: 29px;
    }

    @include small-mobile {
      font-size: 20px;
      line-height: 24px;
    }
  }
}
