.hero {
  background: url('../img/hero-bg.webp');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 40% 50%;
  position: relative;
  padding: 220px 0 100px;

  @include mobile {
    padding: 180px 0 50px;
  }

  &::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: rgba(11, 29, 51, 0.4);
  }

  // @include desktop {
  //   min-height: 100vh;
  // }

  &__container {
    position: relative;
    z-index: 10;
    margin: 0 auto;
    text-align: center;
  }

  &__content {
    margin-bottom: 50px;

    @include tablet {
      margin-bottom: 40px;
    }
  }

  &__subtitle {
    font-weight: 500;
    font-size: 32px;
    line-height: 36px;
    text-transform: uppercase;
    margin-bottom: 25px;

    @include tablet {
      font-size: 24px;
      line-height: 28px;
    }

    @include mobile {
      font-size: 20px;
      line-height: 22px;
    }

    @include small-mobile {
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 15px;
    }
  }

  &__title {
    font-weight: bold;
    font-size: 46px;
    line-height: 50px;
    text-transform: uppercase;
    margin-bottom: 80px;

    @include tablet {
      font-size: 32px;
      line-height: 36px;
    }

    @include mobile {
      font-size: 26px;
      line-height: 30px;
    }

    @include small-mobile {
        font-size: 22px;
        line-height: 24px;
        margin-bottom: 30px;
    }
  }

  &__order {
    font-size: 22px;
    text-transform: uppercase;
    padding: 20px 30px;
    background: $mainColor;
    display: block;
    max-width: 350px;
    max-height: 100px;
    margin: 0 auto;
    transition: all 0.4s;

    &:focus, &:hover {
      background: $mainColorHover;
    }

    @include tablet {
      padding: 20px 30px;
    }

    @include small-mobile {
        padding: 15px 25px;
        max-width: 250px;
        font-size: 18px;
    }
  }

  &-advantages {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: baseline;

    
    @include tablet {
        flex-wrap: wrap;
    }

    &__item {
      margin: 0 auto;
      max-width: 380px;

      @include tablet {
          width: 220px;
      }

      &:not(:last-child) {
        @media (max-width: 510px) {
            margin-bottom: 20px;
            width: 280px;
        }
      }
    }

    &__icon {
      margin-bottom: 20px;
      svg {
          width: 100%;
          max-width: 60px;
          max-height: 60px;
      }

      @include tablet {
          max-width: 40px;
          max-height: 40px;
          margin: 0 auto 16px;
      }
    }

    &__text {
      font-weight: bold;
      font-size: 20px;
      line-height: 1.4rem;
      text-align: center;
      letter-spacing: 0.15em;
      text-transform: uppercase;

      @include tablet {
        font-size: 11.3422px;
      }
    }
  }
}
