.header {
  position: absolute;
  transform: translateX(-50%);
  left: 50%;
  top: 50px;
  width: 85%;
  z-index: 30;

  @include mobile {
    top: 30px;
    width: 95%;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include small-mobile {
      justify-content: flex-end;
      flex-wrap: wrap;
    }
  }

  &__left {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;

    @include small-mobile {
      margin-bottom: 15px;
      padding-right: 10px;
    }

    .logotype {
      width: 80px;
      height: auto;

      img {
        object-fit: cover;
      }

      @include mobile {
        width: 50px;
      }
    }
  }

  &__right {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-contacts {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__order {
      margin-right: 35px;
      padding: 15px 30px;
      font-weight: normal;
      font-size: 16px;
      line-height: 1.22rem;
      text-transform: uppercase;
      transition: all 0.4s;

      &:focus,
      &:hover {
        background: rgba($mainColor, 0.1);
      }

      @include big-tablet {
        margin-right: 20px;
      }

      @include tablet {
        font-size: 16px;
        padding: 12px 20px;
      }

      @include mobile {
        font-size: 14px;
        margin-right: 10px;
      }

      @media (max-width: 410px) {
        font-size: 10px;
        padding: 10px 10px;
        margin-right: 10px;
      }
    }

    &__phones {
      display: flex;
      align-items: center;
      margin-right: 40px;

      @include big-tablet {
        margin-right: 30px;
      }

      @include tablet {
        flex-direction: column;
      }

      @media (max-width: 440px) {
        margin-right: 20px;
      }
    }

    &__phone {
      display: block;
      text-transform: lowercase;
      font-size: 16px;
      line-height: 1.22rem;
      cursor: pointer;
      white-space: nowrap;

      @include small-mobile {
        font-size: 13px;
      }

      &:first-child {
        margin-right: 20px;

        @include big-tablet {
          margin-right: 15px;
        }

        @include tablet {
          margin-right: 0;
        }
      }
    }
  }
}

.menu__body {
  position: absolute;
  width: 30vw;
  height: 110vh;
  overflow: auto;
  right: -100%;
  top: -50px;
  background-color: $blueColor;
  flex-direction: column;
  justify-content: center;
  transition: all .6s ease;
  align-items: center;
  display: flex;
  margin: 0 auto;
  padding: 80px 50px;
  text-align: center;
  opacity: 0;
  visibility: hidden;

  @include tablet {
    width: 50vw;
  }

  @include mobile {
    padding: 50px 30px;
    width: 60vw;
  }

  @include small-mobile {
    width: 70vw;
  }

  .menu__item {
    font-size: 30px;
    line-height: 34px;
    font-family: inherit;
    cursor: pointer;
    transition: all .8s ease-in;

    &:focus,
    &:hover {
      text-decoration: underline;
    }

    &:not(:last-child) {
      margin-bottom: 40px;

      @include mobile {
        margin-bottom: 20px;
      }
    }

    @include big-tablet {
      font-size: 28px;
      line-height: 32px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 20px;
    }
  }
}

.menu__body._active {
  right: -10%;
  z-index: 40;
  opacity: 1;
  visibility: visible;
}

.icon-menu {
  z-index: 100;
}
