.questions {
  padding: 120px 0 70px;
  background: url('../img/questions-bg.webp');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;

  @include mobile {
    padding: 70px 0 50px;
  }


  @include small-mobile {
    padding: 60px 0 40px;
  }

  &::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, rgba(11, 29, 51, 0.2) 0%, rgba(11, 29, 51, 0.8) 0.03%, rgba(11, 29, 51, 0.3) 100%);

    @include mobile {
      background: rgba(11, 29, 51, 0.5);
    }
  }

  &__container {
    position: relative;
    z-index: 10;
  }

  &__content {
    margin-bottom: 80px;

    @include small-mobile {
      margin-bottom: 60px;
    }
  }

  &__title {
    font-weight: 300;
    font-size: 36px;
    line-height: 40px;
    margin-bottom: 35px;
    text-transform: uppercase;

    @include tablet {
      font-size: 32px;
      line-height: 36px;
    }

    @include mobile {
      font-weight: 300;
      font-size: 28px;
      line-height: 32px;
      text-align: center;
      margin-bottom: 10px;
    }


    @include small-mobile {
      font-size: 24px;
      line-height: 28px;
    }

    span {
      color: $mainColor;
      font-weight: 700;
    }
  }

  &__subtitle {
    font-size: 26px;
    line-height: 150%;
    color: rgba($whiteColor, 0.7);
    text-transform: uppercase;
    max-width: 630px;
    margin-bottom: 55px;

    @include mobile {
      font-size: 22px;
      text-align: center;
    }

    @include small-mobile {
      font-size: 18px;
      margin-bottom: 40px;
    }

    span {
      font-weight: 700;
    }
  }

  &__button {
    font-weight: normal;
    font-size: 20px;
    line-height: 22px;
    padding: 25px 35px;
    background: $mainColor;
    display: block;
    max-width: 330px;
    text-align: center;

    @include mobile {
      font-size: 18px;
      line-height: 22px;
      padding: 20px 30px;
      max-width: 325px;
      margin: 0 auto;
    }

    @include small-mobile {
      padding: 14px 16px;
    }
  }

  &-benefits {
    display: flex;
    align-items: center;

    @include small-mobile {
      flex-direction: column;
      align-items: start;
    }

    &__img {
      display: block;
      margin-right: 20px;

      img {
        border-radius: 10px;
        max-width: 200px;
        max-height: 110px;
        object-fit: cover;

        @include small-mobile {
          max-width: 150px;
          max-height: 100px;
          margin-bottom: 15px;
        }
      }
    }

    &__text {
      max-width: 400px;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      text-transform: uppercase;
      color: rgba($whiteColor, 0.7);

      @include mobile {
        font-size: 14px;
        line-height: 20px;
      }
    }
  }
}


.question-form__content {
  background: #FFFFFF;
  border-radius: 25px;
  padding: 40px;

  @media (max-width: 1140px) {
    padding: 30px;
  }

  @include tablet {
    padding: 25px;
  }

  @include small-mobile {
    padding: 16px;
  }
}

.question {
  &__close {
    width: 30px;
    height: 30px;
    position: absolute;
    top: -25px;
    right: -45px;
    cursor: pointer;
    z-index: 30;
    background: url(../img/close.svg) 0 0 no-repeat;
    background-size: cover;

    @include tablet {
      right: -25px;
    }

    @include mobile {
      width: 18px;
      height: 18px;
      right: -15px;
      right: -15px;
    }
  }

  &__title {
    font-weight: bold;
    font-size: 36px;
    line-height: 40px;
    color: #000000;
    margin-bottom: 25px;

    &-accent {
      color: $mainColor;
    }

    @media (max-width: 1140px) {
      font-size: 28px;
      line-height: 30px;
    }

    @include tablet {
      font-size: 26px;
      line-height: 30px;
    }

    @include mobile {
      font-size: 24px;
      line-height: 28px;
    }

    @include small-mobile {
      font-size: 22px;
      line-height: 24px;
      margin-bottom: 15px;
    }
  }

  &__descr {
    font-weight: 300;
    font-size: 22px;
    line-height: 26px;
    color: rgba(0, 0, 0, 0.8);
    margin-bottom: 25px;

    span {
      font-weight: bold;
      color: #000000;
    }

    @include mobile {
      font-size: 18px;
      line-height: 22px;
      margin-bottom: 20px;
    }

    @include small-mobile {
      font-size: 16px;
      line-height: 18px;
      margin-bottom: 15px;
    }
  }

  &__subdescr {
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: rgba(0, 0, 0, 0.7);
    margin-bottom: 25px;

    @include tablet {
      margin-bottom: 15px;
      font-size: 18px;
      line-height: 22px;
    }
  }

  &__content {
    margin-bottom: 25px;
  }

  &__buttons {
    display: flex;
    align-items: center;
  }

  &-button {
    font-weight: normal;
    font-size: 20px;
    line-height: 24px;
    text-transform: uppercase;
    border: 1px solid $blueColor;
    border-radius: 50px;
    display: block;
    padding: 15px 30px;
    width: 100%;
max-width: 300px;
display: block;
    cursor: pointer;
    text-align: center;

    @include tablet {
      padding: 15px 20px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 22px;
      padding: 10px 20px;
    }

    @include small-mobile {
      padding: 10px 16px;
      font-size: 18px;
      line-height: 20px;
    }
  }

  &-back {
    color: #000000;
    background: $whiteColor;
    margin-right: 35px;
    @include mobile {
      margin-right: 20px;
    }
    @include small-mobile {
      margin-right: 20px;
    }
    @media (max-width: 340px) {
      margin-right: 15px;
    }
  }

  &-next {
    background: $blueColor;
    color: $whiteColor;
  }

  &-order {
    background: $blueColor;

    @include mobile {
      padding: 15px 20px;
      font-size: 22px;
      line-height: 24px;
    }


    @include small-mobile {
      font-size: 16px;
      line-height: 20px;
      padding: 14px 20px;
    }
  }

  &__policy {
    font-weight: 300;
    font-size: 18px;
    line-height: 20px;
    color: #000000;
    max-width: 500px;
    display: block;
    position: relative;
    padding-left: 30px;

    &::before {
      content: "";
      display: inline-block;
      width: 20px;
      height: 20px;
      flex-shrink: 0;
      flex-grow: 0;
      background: #D3D3D3;
      border: 1px solid #ABABAB;
      margin-right: 12px;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 100%;
      position: absolute;
      top: 15px;
      left: 0;

      @include small-mobile {
        width: 16px;
        height: 16px;
      }
    }

    &::after {
      content: "";
      width: 16px;
      height: 16px;
      position: absolute;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 100%;
      top: 17px;
      left: 2px;
      z-index: 10;

      @include small-mobile {
        width: 14px;
        height: 14px;
      }
    }

    &-input:checked+label::after {
      background: url('../img/policy-check.svg');
      background-size: cover;
      background-repeat: no-repeat;
    }

    &-input {
      position: absolute;
      z-index: -1;
      opacity: 0;
    }

    a {
      font-weight: 500;
      text-decoration-line: underline;
    }

    @include tablet {
      max-width: 80%;
      font-size: 22px;
      line-height: 26px;
    }

    @include mobile {
      max-width: 100%;
      font-size: 16px;
      line-height: 18px;
      padding-left: 20px;
    }

    @include small-mobile {
      font-size: 14px;
      line-height: 16px;
    }
  }
}

.answers {
  &-item {
    position: relative;

    label::after {
      content: "";
      width: 16px;
      height: 16px;
      position: absolute;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: cover;
      top: 5px;
      left: 2px;
      z-index: 10;

      @media (max-width: 1140px) {
        width: 14px;
        height: 12px;
        top: 3px;
        left: 2px;
      }

      @include tablet {
        width: 14px;
        height: 12px;
        top: 4px;
        left: 2px;
      }
    }

    input:checked+label::after {
      background: url('../img/policy-check.svg');
      background-size: cover;
    }

    input {
      position: absolute;
      z-index: -1;
      opacity: 0;
    }
  }

  &_first {
    display: flex;
    justify-content: space-between;
    gap: 30px;

    @media (max-width: 1140px) {
      gap: 20px;
    }

    @media (max-width: 600px) {
      gap: 15px;
flex-direction: column;
    }
  }

  &_second {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-template-rows: auto;

    @include tablet {
      grid-template-columns: repeat(2, auto);
    }

    @media (max-width: 500px) {
      grid-template-columns: 1fr;
    }
  }

  &__label {
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    color: #000000;
    margin-bottom: 20px;
    display: block;
    padding-left: 30px;

    &::before {
      content: "";
      display: inline-block;
      width: 20px;
      height: 20px;
      flex-shrink: 0;
      flex-grow: 0;
      background: #D3D3D3;
      border: 1px solid #ABABAB;
      margin-right: 12px;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 100%;
      position: absolute;
      top: 3px;
      left: 0;


      @media (max-width: 1140px) {
        width: 18px;
        height: 16px;
      }
    }


    @media (max-width: 1140px) {
      margin-bottom: 20px;
      font-size: 20px;
      line-height: 24px;
    }

    @include tablet {
      margin-bottom: 15px;
      font-size: 18px;
      line-height: 22px;
    }

    @include mobile {
      margin-bottom: 10px;
      font-size: 16px;
      line-height: 20px;
    }
  }
}

.answer__input {
  input {
    font-family: inherit;
    font-weight: 300;
    font-size: 22px;
    line-height: 26px;
    color: #000000;
    padding: 16px 20px;
    border: 1px solid $blueColor;
    border-radius: 12px;
    width: 100%;
    max-width: 300px;

    @include mobile {
      padding: 14px 16px;
      max-width: 250px;
    }


    @include small-mobile {
      padding: 10px 10px;
      max-width: 240px;
      font-size: 18px;
      line-height: 20px;
    }
  }

  margin-bottom: 25px;

}
