.connection {
  padding: 60px 0 55px;
  background: url('../img/connection-bg.webp');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.35));

  @include mobile {
    padding: 30px 0 40px;
  }

  &::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: rgba(4, 14, 27, 0.7);

    @include mobile {
      background: rgba(11, 29, 51, 0.7);
    }
  }

  &__container {
    position: relative;
    z-index: 10;
  }

  &__title {
    font-weight: 600;
    font-size: 32px;
    line-height: 170%;
    margin-bottom: 50px;

    @include small-mobile {
      margin-bottom: 30px;
      font-size: 24px;
      line-height: 28px;
    }
  }

  &-form {
    margin: 0 auto;
    max-width: 800px;

    &__item {
      width: 100%;
      height: 40px;

      &:not(:last-child) {
        margin-bottom: 40px;
      }
    }

    &__button {
      padding: 25px 40px;
      max-width: 350px;
      font-weight: normal;
      font-size: 24px;
      line-height: 28px;
      background: $mainColor;
      display: block;
      margin: 50px auto 0;
      transition: all 0.4s;
      width: 100%;
      display: block;
      text-align: center;

      &:focus,
      &:hover {
        background: $mainColorHover;
      }

      @include small-mobile {
        padding: 15px 20px;
        font-size: 20px;
        line-height: 22px;
        margin: 30px auto 0;
      }
    }

    &__label {
      display: block;
      margin: 0 auto;
      font-weight: bold;
      font-size: 18px;
      line-height: 180%;
      letter-spacing: 0.15em;
      color: $whiteColor;
      text-align: center;
    }

    &__input {
      font-family: inherit;
      background: none;
      border: none;
      border-bottom: 2px solid #fff;
      width: 100%;
      height: 40px;
      font-weight: bold;
      font-size: 18px;
      line-height: 180%;
      letter-spacing: 0.15em;
      color: $whiteColor;
      text-align: center;
      resize: none;
      outline: none;
      &::placeholder {
        color: $whiteColor;
        opacity: 1;
      }
    }
  }
}
