.advantages {
  padding: 60px 0 60px;
  background: url('../img/advantages-bg.webp') #061323;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  position: relative;

  @include mobile {
    padding: 40px 0 50px;
  }

  &::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: rgba(11, 29, 51, 0.7);

    @include mobile {
      background: rgba(11, 29, 51, 0.7);
    }
  }

  &__container {
    position: relative;
    z-index: 10;
  }

  &__title {
    font-weight: 600;
    font-size: 32px;
    line-height: 38px;
    text-transform: uppercase;
    margin-bottom: 50px;
    max-width: 800px;

    span {
      color: $mainColor;
    }

    @include mobile {
      text-align: center;
      font-size: 26px;
      line-height: 30px;
      margin-bottom: 50px;
    }

    @include small-mobile {
      font-size: 22px;
      line-height: 24px;
    }
  }

  &__items {
    max-width: 900px;
    display: flex;
    flex-direction: column;
    margin-bottom: 40px;
    list-style: none;
  }

  &__item {
    font-weight: normal;
    font-size: 24px;
    line-height: 28px;
    position: relative;
    padding-left: 50px;

    @include mobile {
      font-size: 18px;
      line-height: 22px;
      padding-left: 40px;
    }

    @include small-mobile {
      font-size: 16px;
      padding-left: 30px;
    }

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background: url('../img/check-icon.png');
      background-repeat: no-repeat;
      background-size: cover;
      width: 30px;
      height: 30px;

      @include mobile {
        width: 20px;
        height: 20px;
      }

      @include small-mobile {
        width: 20px;
        height: 20px;
      }
    }
  }

  &__question {
    display: block;
    font-weight: normal;
    font-size: 24px;
    line-height: 30px;
    text-transform: uppercase;
    padding: 20px 35px;
    max-width: 350px;
    background: $mainColor;
    text-align: center;
    transition: all 0.4s;

    &:focus, &:hover {
      background: $mainColorHover;
    }

    @include mobile {
      font-size: 20px;
      line-height: 22px;
      padding: 15px 20px;
      max-width: 250px;
      margin: 0 auto;
    }
  }
}
