.location {
  &__top {
    padding: 20px 0 30px;

    @include mobile {
      padding: 10px 0 20px;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 32px;
    line-height: 36px;

    @include small-mobile {
      font-size: 24px;
      line-height: 28px;
    }
  }

  &__container {
    iframe {
      width: 100%;
      height: 600px;

      @include tablet {
        height: 400px;
      }

      @include mobile {
        height: 350px;
      }
    }
  }
}

.location-bottom {
  padding: 25px 0 30px;
  display: grid;
  row-gap: 30px;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  column-gap: 20px;

  @include tablet {
    grid-template-columns: repeat(2, 1fr);
  }

  @include mobile {
    grid-template-columns: auto;
    row-gap: 15px;
  }

  &__item {
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: rgba($whiteColor, 0.6);

    @include mobile {
      font-size: 16px;
      line-height: 20px;
    }
  }

  // &__address {
  //   text-transform: uppercase;
  // }
}

.location-socials {
  display: flex;
  align-items: center;

  a {
    display: block;
    width: 30px;
    height: 30px;

    @include small-mobile {
      width: 20px;
      height: 20px;
    }

    img {
      width: 100%;
      height: 100%;
    }

    &:not(:last-child) {
      margin-right: 40px;

      @include mobile {
        margin-right: 20px;
      }
    }
  }

  &__items {
    display: flex;
    align-items: center;
  }
}
