html {
  box-sizing: border-box;
  background-color: $blueColor;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  font-family: 'Montserrat', 'Roboto', sans-serif;
  font-display: swap;
  color: $whiteColor;
}

body._lock {
  overflow: hidden;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  cursor: pointer;
  font-family: inherit;
  color: inherit;
}

a,
button,
input,
textarea {
  color: inherit;
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5 {
  display: block;
}

.container {
  margin: 0 auto;
  // padding: 0 15px;
  max-width: 85%;
}

.site-container {
  position: relative;
  overflow-x: hidden;
}

.btn {
  border: 1px solid $mainColor;
  filter: drop-shadow(0px 0px 5px rgba($mainColor, 0.8));
  border-radius: 12px;
  white-space: nowrap;
}


.line-title {
  position: relative;
  color: $mainColor;
  padding-left: 55px;

  @include mobile {
    position: static;
    padding-left: 0;
    text-align: center;
  }

  &:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 40px;
    height: 3px;
    background: #FFFFFF;

    @include mobile {
      display: none;
    }
  }
}

.logo {
  font-weight: bold;
  font-size: 22px;
  line-height: 24px;
  text-transform: uppercase;
  text-shadow: 0px 0px 4px rgba(255, 210, 210, 0.5);

  @include mobile {
    font-size: 14px;
    line-height: 16px;
  }
}