.credit {
  padding: 80px 0 90px;
  background: url('../img/credit-bg.webp');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;

  @include mobile {
    padding: 60px 0;
  }

  @include small-mobile {
    padding: 40px 0;
  }

  &::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.4));

    @include mobile {
      background: rgba(11, 29, 51, 0.5);
    }
  }

  &__container {
    position: relative;
    z-index: 10;
  }

  &__title {
    font-weight: 700;
    font-size: 30px;
    line-height: 143%;
    letter-spacing: 0.15em;
    margin-bottom: 25px;
    max-width: 1000px;

    @include mobile {
      font-size: 24px;
      text-align: center;
    }

    @include small-mobile {
      font-size: 22px;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 20px;
    line-height: 143%;
    letter-spacing: 0.15em;
    max-width: 950px;
    margin-bottom: 80px;

    @include mobile {
      font-size: 18px;
      text-align: center;
      margin-bottom: 60px;
    }

    @include small-mobile {
      font-size: 16px;
      margin-bottom: 40px;
    }

    p {
      &:first-child {
        margin-bottom: 20px;
      }
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 30px;

    @include mobile {
      flex-direction: column;
      gap: 20px;
    }
  }

  &__button {
    max-width: 600px;
    padding: 20px 35px;
    font-weight: normal;
    font-size: 22px;
    line-height: 26px;
    transition: all 0.4s;
    &:first-child {
      background: $mainColor;
    }

    &_payment {
      &:focus, &:hover {
        background: $mainColorHover;
      }
    }

    &_circs {
      &:focus, &:hover {
        background: rgba($mainColor, 0.1);
      }
    }

    @include mobile {
      font-size: 24px;
      line-height: 29px;
    }

    @media (max-width: 600px) {
      padding: 18px 15px;
      font-size: 16px;
      line-height: 22px;
    }

    @media (max-width: 355px) {
      padding: 16px 10px;
      font-size: 15px;
      line-height: 22px;
    }
  }
}
