.consultation {
  padding: 100px 0;

  @include mobile {
      padding: 60px 0;
  }

  &__items {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 35px;

    @include tablet {
        justify-content: center;
        margin-bottom: 40px;
        flex-wrap: wrap;
        text-align: center;
    }
  }

  &__item {
      max-width: 500px;
      width: 100%;
      &:first-child{
          margin-right: 40px;
          @include tablet {
              margin-right: 0;
              margin-bottom: 40px;
          }
      }
  }

  &__title {
    font-weight: 600;
    font-size: 32px;
    line-height: 36px;
    color: $mainColor;
    margin-bottom: 30px;

    @include big-tablet {
        font-size: 28px;
        line-height: 32px;
        margin-bottom: 25px;
    }

    @include tablet {
        margin-bottom: 15px;
    }

    @media (max-width: 500px) {
        font-size: 24px;
        line-height: 28px;
    }
  }

  &__text {
    font-weight: normal;
    font-size: 24px;
    line-height: 150%;
    color: rgba(255, 255, 255, 0.7);
    text-align: left;
    
    @include big-tablet {
        font-size: 22px;
    }

    @media (max-width: 500px) {
        font-size: 20px;
    }

    li:not(:last-child) {
        margin-bottom: 20px;

        @include mobile {
            margin-bottom: 10px;
        }
    }
  }

  &__button {
    font-weight: normal;
    font-size: 30px;
    line-height: 32px;
    padding: 25px 30px;
      background: $mainColor;
      display: block;
      max-width: 400px;
      text-align: center;
      margin: 0 auto;
      
    @include big-tablet {
        font-size: 28px;
        line-height: 32px;
        max-width: 350px;
    }

    @include big-tablet {
        font-size: 24px;
        line-height: 30px;
        padding: 20px 30px;
    }

    @media (max-width: 500px) {
        font-size: 20px;
        line-height: 24px;
        padding: 20px 30px;
        max-width: 300px;
    }
  }
}
